.add-cell{
  position: relative;
  opacity: 0;
  transition: opacity 0.3s ease-in 0.2s;
}

/* .add-cell:active {
  opacity: 0 !important;
  transition: opacity 0s;
} */

.add-cell:hover {
  opacity: 1.0;
}

.add-cell.force-visible{
  opacity: 1.0;
}

.add-cell .divider {
  position: absolute;
  top: 50%;
  bottom: 50%;
  border-bottom: 1px solid gray;
  left: 2.5%;
  right: 2.55%;
  width: 95%;
  z-index: -1;
  
}

.add-cell .add-buttons{
  display: flex;
  justify-content: center;
}

.add-cell .add-buttons button {
  margin: 0 50px;
}